let WebIM = require("../../utils/WebIM")["default"];

export default {
  data() {
    return {
      username: "",
      password: "",
      psdFocus: "",
      nameFocus: ""
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    let app = getApp().globalData;
  },
  methods: {
    bindUsername: function (e) {
      this.setData({
        username: e.detail.value
      });
    },
    bindPassword: function (e) {
      this.setData({
        password: e.detail.value
      });
    },
    onFocusPsd: function () {
      this.setData({
        psdFocus: 'psdFocus'
      });
    },
    onBlurPsd: function () {
      this.setData({
        psdFocus: ''
      });
    },
    onFocusName: function () {
      this.setData({
        nameFocus: 'nameFocus'
      });
    },
    onBlurName: function () {
      this.setData({
        nameFocus: ''
      });
    },
    register: function () {
      const that = this;

      if (that.username == "") {
        return uni.showToast({title: "请输入用户名！",icon:'none'});
      } else if (that.password == "") {
        return uni.showToast({title: "请输入密码！",icon:'none'});
      } else {
        var options = {
          apiUrl: WebIM.config.apiURL,
          username: that.username.toLowerCase(),
          password: that.password,
          nickname: "",
          appKey: WebIM.config.appkey,
          success: function (res) {
            console.log('res', res);
            uni.showToast({title: "注册成功"});
            var data = {
              apiUrl: WebIM.config.apiURL,
              user: that.username.toLowerCase(),
              pwd: that.password,
              grant_type: "password",
              appKey: WebIM.config.appkey
            };
            wx.setStorage({
              key: "myUsername",
              data: that.username
            });
          },
          error: function (res) {
            console.log('res', res);

            if (res.statusCode !== "200") {
              if (res.statusCode == '400' && res.data.error == 'illegal_argument') {
                return uni.showToast({title: "用户名非法",icon:'none'});
              }

              uni.showToast({title: "用户名已被占用",icon:'none'});
            }
          }
        };
        WebIM.conn.registerUser(options);
      }
    }
  }
};
